// فئات المدونة
const categories = [
  'الكل',
  'النماذج اللغوية',
  'توليد الصور',
  'تطوير التطبيقات',
  'تحليل البيانات',
  'إنتاج الفيديو',
  'تقنيات الصوت',
  'أخلاقيات الذكاء الاصطناعي'
];

export default function BlogPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [activeCategory, setActiveCategory] = useState('الكل');
  
  // فلترة المقالات حسب البحث والفئة
  const filteredPosts = blogPosts.filter((post) => {
    const matchesSearch = searchTerm === '' || 
      post.title.includes(searchTerm) || 
      post.excerpt.includes(searchTerm) ||
      post.tags.some(tag => tag.includes(searchTerm));
    
    const matchesCategory = activeCategory === 'الكل' || post.category === activeCategory;
    
    return matchesSearch && matchesCategory;
  });
  
  return (
    <div className="min-h-screen flex flex-col">
      <Navbar />
      
      <main className="flex-grow bg-gray-50">
        <div className="max-w-7xl mx-auto py-10 px-4 sm:px-6 lg:px-8">
          {/* رأس الصفحة */}
          <div className="text-center mb-12">
            <h1 className="text-3xl font-bold text-gray-900 mb-4">مدونة عَرَبَاي</h1>
            <p className="max-w-3xl mx-auto text-xl text-gray-600">
              أحدث المقالات والأدلة حول تقنيات الذكاء الاصطناعي وكيفية استخدامها باللغة العربية
            </p>
            
            {/* شريط البحث */}
            <div className="mt-8 max-w-xl mx-auto">
              <div className="relative">
                <input
                  type="text"
                  placeholder="ابحث في المدونة..."
                  value={searchTerm}
                  onChange={(e) => setSearchTerm(e.target.value)}
                  className="w-full px-5 py-3 border border-gray-300 rounded-full focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:border-transparent pr-12"
                />
                <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
                  <MagnifyingGlassIcon className="h-5 w-5 text-gray-400" />
                </div>
              </div>
            </div>
          </div>
          
          {/* فلتر الفئات */}
          <div className="flex flex-wrap justify-center gap-2 mb-12">
            {categories.map((category) => (
              <button
                key={category}
                onClick={() => setActiveCategory(category)}
                className={`px-4 py-2 rounded-full text-sm font-medium ${
                  activeCategory === category
                    ? 'bg-indigo-600 text-white'
                    : 'bg-white text-gray-700 hover:bg-gray-100'
                }`}
              >
                {category}
              </button>
            ))}
          </div>
          
          {/* المقالات المميزة */}
          {activeCategory === 'الكل' && searchTerm === '' && (
            <div className="mb-16">
              <h2 className="text-2xl font-bold text-gray-900 mb-8">المقالات المميزة</h2>
              
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                  <div className="md:flex">
                    <div className="md:flex-shrink-0 relative h-60 md:h-auto md:w-1/2">
                      <Image
                        src="/images/blog/ai_writing.jpg"
                        alt="كيفية استخدام GPT-4 لكتابة محتوى إبداعي باللغة العربية"
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="p-6 md:w-1/2">
                      <div className="flex items-center text-sm text-gray-500 mb-2">
                        <span className="bg-indigo-100 text-indigo-800 text-xs px-2 py-1 rounded-full">
                          النماذج اللغوية
                        </span>
                        <span className="mx-2">•</span>
                        <ClockIcon className="h-4 w-4 ml-1" />
                        <span>5 دقائق</span>
                      </div>
                      <Link href={`/blog/1`} className="block mt-2">
                        <h3 className="text-xl font-semibold text-gray-900 hover:text-indigo-600">
                          كيفية استخدام GPT-4 لكتابة محتوى إبداعي باللغة العربية
                        </h3>
                        <p className="mt-3 text-gray-600 line-clamp-3">
                          تعرّف على أفضل الممارسات لاستخدام نماذج اللغة العملاقة مثل GPT-4 لإنشاء محتوى إبداعي ومتميز باللغة العربية.
                        </p>
                      </Link>
                      <div className="mt-4">
                        <span className="text-sm text-gray-500">15 مايو 2023</span>
                      </div>
                    </div>
                  </div>
                </div>
                
                <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                  <div className="md:flex">
                    <div className="md:flex-shrink-0 relative h-60 md:h-auto md:w-1/2">
                      <Image
                        src="/images/blog/midjourney_guide.jpg"
                        alt="دليل شامل لاستخدام Midjourney لإنشاء صور مذهلة"
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="p-6 md:w-1/2">
                      <div className="flex items-center text-sm text-gray-500 mb-2">
                        <span className="bg-indigo-100 text-indigo-800 text-xs px-2 py-1 rounded-full">
                          توليد الصور
                        </span>
                        <span className="mx-2">•</span>
                        <ClockIcon className="h-4 w-4 ml-1" />
                        <span>8 دقائق</span>
                      </div>
                      <Link href={`/blog/2`} className="block mt-2">
                        <h3 className="text-xl font-semibold text-gray-900 hover:text-indigo-600">
                          دليل شامل لاستخدام Midjourney لإنشاء صور مذهلة
                        </h3>
                        <p className="mt-3 text-gray-600 line-clamp-3">
                          خطوة بخطوة لاستخدام Midjourney بكفاءة لإنشاء صور إبداعية عالية الجودة تناسب مشاريعك المختلفة.
                        </p>
                      </Link>
                      <div className="mt-4">
                        <span className="text-sm text-gray-500">3 يونيو 2023</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )}
          
          {/* قائمة المقالات */}
          <div>
            <h2 className="text-2xl font-bold text-gray-900 mb-8">
              {activeCategory === 'الكل' ? 'أحدث المقالات' : `مقالات ${activeCategory}`}
            </h2>
            
            {filteredPosts.length > 0 ? (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
                {filteredPosts.map((post) => (
                  <article key={post.id} className="bg-white rounded-xl shadow-sm overflow-hidden flex flex-col">
                    <div className="relative h-48">
                      <Image
                        src={post.coverImage}
                        alt={post.title}
                        fill
                        className="object-cover"
                      />
                    </div>
                    <div className="p-6 flex-1 flex flex-col">
                      <div className="flex items-center text-sm text-gray-500 mb-2">
                        <span className="bg-indigo-100 text-indigo-800 text-xs px-2 py-1 rounded-full">
                          {post.category}
                        </span>
                        <span className="mx-2">•</span>
                        <ClockIcon className="h-4 w-4 ml-1" />
                        <span>{post.readTime}</span>
                      </div>
                      <Link href={`/blog/${post.id}`} className="block flex-1">
                        <h3 className="text-xl font-semibold text-gray-900 hover:text-indigo-600 line-clamp-2 mb-2">
                          {post.title}
                        </h3>
                        <p className="text-gray-600 line-clamp-3">
                          {post.excerpt}
                        </p>
                      </Link>
                      <div className="mt-4 pt-4 border-t border-gray-100 flex justify-between items-center">
                        <span className="text-sm text-gray-500">{post.date}</span>
                        <span className="text-sm text-gray-700">{post.author}</span>
                      </div>
                    </div>
                  </article>
                ))}
              </div>
            ) : (
              <div className="text-center py-12 bg-white rounded-lg shadow-sm">
                <h3 className="text-lg font-medium text-gray-900 mb-2">لم يتم العثور على مقالات</h3>
                <p className="text-gray-600">
                  لم نتمكن من العثور على مقالات تطابق معايير البحث الخاصة بك. يرجى تجربة كلمات مفتاحية أخرى أو تصفية أخرى.
                </p>
                <button
                  onClick={() => {
                    setSearchTerm('');
                    setActiveCategory('الكل');
                  }}
                  className="mt-4 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md text-indigo-700 bg-indigo-100 hover:bg-indigo-200"
                >
                  إعادة ضبط الفلاتر
                </button>
              </div>
            )}
          </div>
          
          {/* التوصيات للقراءة */}
          <div className="mt-16">
            <h2 className="text-2xl font-bold text-gray-900 mb-8">موضوعات قد تهمك</h2>
            
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
              {categories.filter(category => category !== 'الكل').slice(0, 4).map((category) => (
                <button
                  key={category}
                  onClick={() => setActiveCategory(category)}
                  className="p-5 bg-white rounded-lg shadow-sm text-center hover:shadow-md transition-shadow"
                >
                  <h3 className="text-lg font-medium text-gray-900 mb-2">{category}</h3>
                  <p className="text-sm text-gray-600">
                    تصفح مقالات {category} المتنوعة لتعلم المزيد عن أحدث التقنيات والممارسات.
                  </p>
                </button>
              ))}
            </div>
          </div>
          
          {/* الاشتراك في النشرة البريدية */}
          <div className="mt-16 bg-indigo-700 rounded-xl shadow-xl overflow-hidden">
            <div className="px-6 py-12 md:px-12 text-center md:text-right">
              <h2 className="text-2xl font-bold text-white mb-4">انضم إلى نشرتنا البريدية</h2>
              <p className="text-indigo-100 mb-6">
                احصل على أحدث المقالات والأدلة حول تقنيات الذكاء الاصطناعي مباشرة إلى بريدك الإلكتروني.
              </p>
              <div className="max-w-md mx-auto md:mx-0 md:mr-auto">
                <div className="flex">
                  <input
                    type="email"
                    placeholder="البريد الإلكتروني"
                    className="w-full px-5 py-3 rounded-r-md focus:outline-none focus:ring-2 focus:ring-white focus:border-transparent"
                  />
                  <button className="px-5 py-3 bg-white text-indigo-700 font-bold rounded-l-md hover:bg-indigo-50">
                    اشتراك
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
      
      <Footer />
    </div>
  );
} 